nav {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}

.toShow {
  display: none;
}


#logo {
  display: block;
  padding-right: 30px;
  float: left;
  font-size: 20px;
  line-height: 60px;
}

nav:after {
  content: "";
  display: table;
  clear: both;
}

nav ul {
  float: right;
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

nav ul li {
  margin: 0px;
  display: inline-block;
  float: left;
  background-color: #ffffff;
}

nav a {
  display: block;
  padding: 0 20px;
  color: black;
  font-size: 20px;
  line-height: 42px;
  text-decoration: none;
}

nav ul li ul li:hover { background: #ffffff; }

nav a:hover { background-color: #ffffff; }

nav ul ul {
  display: none;
  position: absolute;
  top: 42px;
}

nav a.short {
  line-height: 36px;
  font-size: 18px;
}

nav ul li:hover > ul { display: inherit; }

nav ul ul li {
  width: 160px;
  float: none;
  display: list-item;
  position: relative;
}

nav ul ul ul li {
  position: relative;
  top: -42px;
  width: 250px;
  left: 160px;
}

li > a:after { content: ' +'; }

.toggle, [id^=drop] {
  display: none;
}

li > a:only-child:after { content: ''; }


@media all and (max-width : 1090px) {

  #logo {
    padding-right: 5px;
  }

  nav a {
    padding: 0 8px;
    font-size: 16px;
  }

  nav a.short {
    line-height: 28px;
    font-size: 14px;
  }

  nav ul ul li {
    width: 140px;
    float: none;
    display: list-item;
    position: relative;
  }

  nav ul ul ul li {
    top: -42px;
    left: 140px;
    width: 140px;
  }


}
