// Poniżej zakomentowane rozwiązanie z sieci - które było częścią całości - tego co wyżej


@media all and (max-width : 768px) {

  #logo2 {
    display: inline-block;
    padding: 0;
    height: 42px;
    text-align: left;
    float: left;
  }

  nav a {
    max-height: 60px;
    overflow: no-display;
  }


  #logo {
    display: none;
    padding: 0;
    width: 100%;
    text-align: left;
    float: none;
  }

  nav { margin: 0; }

  .toggle + a,
  .menu { display: none; }

  .toggle {
    display: block;
    background-color: #CE242C;
    padding: 0 20px;
    color: white;
    font-size: 16px;
    line-height: 60px;
    text-decoration: none;
    border: none;
  }

  .toggle-menu {
    text-align: right;
    display: block;
  }

  .toggle:hover { background-color: #CE242C; }

  [id^=drop]:checked + ul { display: block; }

  nav ul li {
    display: block;
    width: 100%;
  }

  nav ul ul .toggle,
  nav ul ul a { padding: 0 40px; }

  nav ul ul ul a { padding: 0 80px; }

  nav a:hover,
  nav ul ul ul a { background-color: #CE242C; }

  nav ul li ul li .toggle,
  nav ul ul a { background-color: #CE242C; }

  nav ul ul {
    float: none;
    position: static;
    color: white;
  }

  nav ul ul li:hover > ul,
  nav ul li:hover > ul { display: none; }

  nav ul ul li {
    display: block;
    width: 100%;
  }

  nav ul ul ul li { position: static;

  }
}

@media all and (max-width : 330px) {

  nav ul li {
    display: block;
    width: 94%;
  }

}
